* {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
p,
div,
ul {
  font-family: Tajawal, Plus Jakarta Sans, sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  width: 100vw;
  padding: 22px 14px;
  height: fit-content;
  background-image: url("images/mobile-backgrd.png");
  background-position-x: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.header-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header-logo {
  height: 40px;
  width: 169px;
}

.header-title {
  margin-bottom: 250px;
}

.header-title > span {
  display: block;
  font-size: 30px;
  color: white;

  font-weight: bold;
  text-align: center;
  cursor: default;
  transition: color 0.3s;
}

.header-title > span:hover {
  color: #9eeb00;
}

.header-title-desktop {
  display: none;
}

.header-title-mobile {
  display: block;
}

.form {
  width: 100%;
  padding: 18px 14px 40px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 21px;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(21px);
}

.form-text {
  width: 100%;
  font-size: 23px;
  font-weight: bold;
  line-height: 30px;
  text-align: center;
  margin-bottom: 10px;
  z-index: 2;
  cursor: default;
  color: white;
  transition: color 0.3s;
}

.form-text:hover {
  color: #9eeb00;
}

.form-input {
  text-align: right;
  height: 56px;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-bottom: 29px;
  padding: 0 10px;
  z-index: 2;
}

.form-label {
  font-size: 14px;
  cursor: default;
  z-index: 2;
  margin-bottom: 19px;
  color: white;
  transition: color 0.3s;
}

.form-label:hover {
  color: #9eeb00;
}

.form-ckeckbox {
  width: 15px;
  height: 15px;
  margin-left: 21px;
  border: none;
  z-index: 2;
}

.form-button {
  width: 100%;
  height: 53px;

  font-size: 24px;
  font-weight: bold;
  outline: none;

  border: none;
  border-radius: 5px;

  z-index: 2;
  cursor: pointer;
  background: linear-gradient(180deg, white 0%, #9eeb00 35%);
  transition: color 0.3s, box-shadow 0.3s;
}

.form-button:hover {
  color: white;
  box-shadow: 0 3px 6px #00000029;
}

.main {
  width: 100%;
  position: relative;
  height: fit-content;
  background-image: url("images/main-backgrnd.png");
  background-size: cover;
}

.main-container {
  max-width: 1230px;
  height: fit-content;
  margin: 0 auto;
}

.main-top {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0 5%;
  margin-bottom: 60px;
}

.main-top-content {
  width: 100%;
  height: fit-content;
  margin-bottom: 58px;
}

.main-top-number {
  font-size: 55px;
  font-weight: bold;
  text-align: center;
  background: linear-gradient(180deg, #ba04d3 6%, #8a00fb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-top-text {
  font-size: 38px;
  font-weight: 500;
  text-align: center;
  line-height: 60px;
}

.main-title {
  font-weight: bold;
  color: #87009d;
  padding: 0 5%;
  margin-bottom: 62px;
}

.main-cards {
  display: none;
}

.main-card {
  width: 100%;
  height: 365px;
  border-radius: 30px;
  overflow: hidden;
  padding: 30px;
  margin: 0 auto;
  cursor: pointer;
  box-shadow: 0 2px 10px #00000059;
  background: linear-gradient(180deg, #291040 6%, #e802fb 100%);
  transition: transform 0.3s;
}

.main-card-icon {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.main-card-title {
  font-size: 38px;
  font-weight: bold;
  line-height: 60px;
  text-align: center;
  color: white;
  margin-bottom: 16px;
}

.main-card-text {
  font-size: 22px;
  font-weight: 400;
  line-height: 38px;
  text-align: center;
  color: white;
}

.main-btn-wraper {
  width: 100%;
  padding: 14px;
  background: rgba(0, 0, 0, 0.6);
  position: sticky;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.main-button {
  width: 100%;
  height: 71px;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  outline: none;
  background: linear-gradient(180deg, white 0%, #9eeb00 35%);
  transition: color 300ms, box-shadow 300ms;
}

.main-button:hover {
  color: #fff;
  box-shadow: 0 3px 6px #00000029;
}

.main-slider {
  display: block;
  height: 410px;
  margin-bottom: 20px;
}

.swiper-pagination-bullet {
  display: none;
}

@media (min-width: 1920px) {
  .header-container {
    top: 158px;
  }

  .header-title > span {
    font-size: 44px;
    line-height: 60px;
  }

  .form-text {
    font-size: 25px;
    line-height: 44px;
  }

  .form-input {
    height: 56px;
    margin-bottom: 29px;
  }

  .form-label {
    margin-bottom: 29px;
  }

  .form-button {
    height: 53px;
  }

  .main-cards {
    gap: 83px;
  }

  .main-card {
    width: 354px;
  }

  .main-btn-wraper {
    width: 354px;
  }
}

@media (min-width: 1280px) {
  .header {
    height: 100vh;
    background-image: url("images/desctop-backgrd.png");
    background-position-x: right;
    padding: 0;
  }

  .header-container {
    width: 35%;
    height: fit-content;
    position: absolute;
    right: 10%;
    top: 110px;
  }

  .header-logo {
    height: 61px;
    width: 255px;
    margin-bottom: 31px;
  }

  .header-title {
    margin-bottom: 0;
  }

  .header-title > span {
    margin-bottom: 17px;
    font-size: 34px;
    line-height: 50px;
  }

  .header-title-mobile {
    display: none !important;
  }

  .header-title-desktop {
    display: block;
  }

  .form {
    background: none;
    backdrop-filter: none;
  }

  .form-text {
    width: 500px;
    text-align: center;
    font-size: 15px;
    line-height: 34px;
    margin: 0 auto 22px;
  }

  .form-input {
    width: 328px;
    height: 46px;
    display: block;
    margin: 0 auto 19px;
  }

  .form-label {
    font-size: 15px;
    display: block;
    margin: 0 auto 19px;
  }

  .form-ckeckbox {
    margin-left: 10px;
  }

  .form-button {
    display: block;
    margin: 0 auto;
    width: 328px;
    height: 43px;
  }

  .main {
    padding: 83px 0 109px;
  }

  .main-top {
    flex-direction: row;
  }

  .main-top-content {
    width: 30%;
  }

  .main-cards {
    width: 100%;
    gap: 59px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 87px;
  }

  .main-card {
    width: 30%;
    height: 354px;
    margin: 0;
  }

  .main-card:hover {
    transform: scale(1.02);
  }

  .main-btn-wraper {
    margin: 0 auto;
    position: relative;
    background: none;
    width: 30%;
  }

  .main-slider {
    display: none;
  }
}
